.container {
    background-color: #f9f9f3;
    display: block;
    padding: 80px 0;
    margin: 0 auto;
    width: 100%;
}

.title {
    font-size: 30px;
    line-height: 34px;
    margin: 0;
    margin-bottom: 20px;
    text-align: center;
}

.subtitle {
    color: #4c4b4b;
    font-size: 16px;
    line-height: 24px;
    text-align: center;
    margin: 0;
    margin-bottom: 30px;
}

.userList {
    list-style: none;
    padding: 0;
    margin: 0;
    margin-bottom: 40px;
}

.userItems {
    margin: 0 auto;
    padding: 0;
    width: 216px;
    margin-bottom: 40px;
}
.userItems:last-child {
    margin-bottom: 0;
}

.showMore {
    display: block;
    margin: 0 auto;
    width: 210px;
    background-color: #ef5b4c;
    padding: 16px 0;
    border: none;
    outline: none;
    border-radius: 4px;
    font-size: 16px;
    line-height: 16px;
    cursor: pointer;
    color: #fefefe;
}

.showMore:hover {
    background-color: #d24335;
}

.disableButton {
    composes: showMore;
    color: #8d8c8c;
    background-color: #d7d7d7;
}

.disableButton:hover {
    background-color: #d7d7d7;
}

@media screen and (min-width: 768px ) {
    .container {
        padding: 120px 0;
    }

    .title {
        font-size: 40px;
        line-height: 44px;
        margin-bottom: 78px;
    }

    .userList {
        display: flex;
        margin: 0 auto;
        margin-bottom: 40px;
        max-width: 768px;
        flex-wrap: wrap;
        justify-content: space-between;
    }

    .userItems {
        margin-bottom: 60px;
    }

    .userItems:nth-last-child(-n + 3) {
        margin-bottom: 0;
    }
}

@media screen and (min-width: 1280px) {
    .container {
        min-width: 964px;
        max-width: 1024px;
        padding: 160px 0;
    }

    .userList {
        margin-bottom: 34px;
    }
}

